body {
  --rp-primary: rgb(56, 56, 56);
  --rp-secondary: rgb(255, 255, 255);
  --rp-theme: rgb(19, 164, 100);
  --player-button-height: 50px;
}

.echo-audio-player {
  font-family: Source Sans Pro, Arial, Liberation Sans, Helvetica, "sans serif";
  position: fixed;
  bottom: 0;
  z-index: 9999;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  transition: all 300ms ease-in-out;
  transform: translateY(100%);
}

.echo-audio-player.opened {
  transform: translateY(0);
}

.audio-bar {
  padding: 13px 10px;
  background-color: var(--rp-primary);
  width: 100%;
}

.audio-container {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  padding-right: 100px;
  padding-left: 100px;
}

.audio-container .rp-logo {
  width: 8rem;
}

.audio-container .close-button {
  color: var(--rp-secondary);
  width: 2rem;
  height: 2rem;
}

.audio-open {
  color: var(--rp-secondary);
  background: var(--rp-theme);
  padding-left: 40px;
  padding-right: 40px;
  height: var(--player-button-height);
  text-transform: uppercase;
  font-size: 20px;
  font-weight: 500;
  transition: all 300ms ease-in-out;
}

.audio-open:hover {
  color: var(--rp-secondary);
  background: var(--rp-primary);
}

.controls-container {
  display: flex;
  justify-content: center;
  gap: 2rem;
}

.state-icon {
  color: var(--rp-secondary);
}

.audio-control {
  color: var(--rp-theme);
}

.audio-control:disabled, .audio-control:disabled:hover {
  opacity: 75%;
  color: var(--rp-secondary);
}

.audio-control:not(:disabled):hover {
  color: var(--rp-secondary);
}

.audio-control svg,
.state-icon {
  height: 2rem;
  width: 2rem;
}

.radioButton {
  background: #47eaa3;
  color: #f7f7f7;
  height: 2.5em;
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 0.5rem;
  padding: 0 1rem;
  border-radius: 10px;
  border-width: 0 3px 3px 1px;
  cursor: pointer;
  border-style: solid;
  border-color: #3ac488;
  transition: all 0.3s ease-in-out;
  margin-bottom: 10px;
}

.radioButton:hover {
  border-width: 0 2px 2px 0;
}

.radioButton:active {
  border-width: 0 0px 0px 0;
}

.radioButton svg {
  fill: #f7f7f7;
  height: 40%;
}

.radioButtonInner {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  height: 100%;
}